import React from 'react'

import { Box, AppBar, Toolbar, styled, InputBase } from '@mui/material';
import { Menu as MenuIcon, Tune, HelpOutlineOutlined, SettingsOutlined, AppsOutlined, AccountCircleOutlined, Search } from '@mui/icons-material'

import { gmailLogo } from '../constants/constants';

const StyledAppBar = styled(AppBar)({
  background: '#f5f5f5',
  boxShadow: 'none'
})

const SearchWrapper = styled(Box)`
  background: #EAF1FB;
  margin-left: 80px;
  border-radius: 8px;
  min-width: 690px;
  max-width: 720px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  > div {
    width: 100%;
    padding: 0 10px;
  }
`
const OptionsWrapper = styled(Box)`
  width: 100%;
  display: flex;
  justify-content: end;
  & > svg {
    margin-left: 20px; 
  }
`

const Header = ({ toggleDrawer }) => {
  return (
      <StyledAppBar position='static'>
        <Toolbar>
          <MenuIcon color='action' onClick={toggleDrawer} />
            <img src={gmailLogo} alt='gmail' style={{ width: 110, marginLeft: '15px'}}/>
        <SearchWrapper>
          <Search color='action'/>
          <InputBase placeholder='Search mail'/>
          <Tune color='action'/>
        </SearchWrapper>
        <OptionsWrapper>
          <HelpOutlineOutlined color='action'/>
          <SettingsOutlined color='action'/>
          <AppsOutlined color='action'/>
          <AccountCircleOutlined color='action'/>
        </OptionsWrapper>
        </Toolbar>
      </StyledAppBar>
  )
}

export default Header